export const toDays = (time) => Math.floor(time / 1000 / 60 / 60 / 24);
const months = [
    'января',
    'февраля',
    'марта',
    'апреля',
    'мая',
    'июня',
    'июля',
    'августа',
    'сентября',
    'октября',
    'ноября',
    'декабря'
];
export function formatDate(date) {
    const today = new Date();
    today.setHours(0);
    if (toDays(date.getTime()) === toDays(today.getTime())) {
        return 'сегодня';
    }
    return `${date.getDate()} ${months[date.getMonth()]}`;
}
